:root{
  --colorPrincipal: #4392F1;
}

/* Estilos globales */
a{
  text-decoration: none;
}

/* Header */
header{
  text-align: center;
}
header nav ul{
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: space-evenly;
}
header nav ul li{
  font-size: 25px;
}
header .foto{
  margin: 0 auto;
  position: relative;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  overflow: hidden;
}
header h1 span{
  display: block;
}
.foto img{
  position: absolute;
  height: 100%;
  left: -50px;
}

/* iconos */
.fiverr{
  color: #1DBF73;
}
.fa-linkedin-in{
  color: #0077B5;
}
.fa-instagram{
  color: #CE2589;
}
.fa-twitter{
  color: #1DA1F2;
}
.fa-facebook-f{
  color: #4267B2;
}